<script type="es6">
import { mapActions, mapState } from "vuex";
import FontCon from "../../components/Footer";
import HeadTop from "../../components/Header";
import Web3 from "web3";
export default {
  data() {
    return {
      lang:"",
      time: 43984932483,
      money:"",
      input_cols:false,
      timer:null,
      timer2:null,
      // progressWidth: 30,
    };
  },
    components: {
    HeadTop,
    FontCon,
  },
  computed: {
    ...mapState("main", [
      "dogesInfo",
      "Balance",
      "FIVEBalance",
      "account",
      // "balance",
      "poolContract",
      // "mxtContract",
      // "hmdBalance",
    ]),
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_DOGESpool",
      "add_dogCoinContract",
      // "add_hmdCoinContract",
    ]),
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem("lang", lang);
      // this.lang = lang;
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
     goTo(){
 //直接跳转
 this.$router.push('/');
 },
    getFiveNum(num){
       var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      if(num){
        console.log()
       return (num*10**18) / this.dogesInfo.info[7]*this.dogesInfo.info[6];
        
      }else{
        return 0;
      }
    },
    getFIVE(num1,num2){
      var num = web3.utils.fromWei(num1, 'ether');
       var a = web3.utils.fromWei(num2, 'Gwei');
      return Number(num*a)
     
    },
    inputEnter(){
      var _this =this;
      if (_this.dogesInfo == null) {
        _this.$toast(_this.$t("message.noMask"));
        return;
      }
      var c = _this.money .toString();
      var a =c.split('.')[1];
      var b = (web3.utils.fromWei(_this.dogesInfo.info[7], 'ether')).split('.')[1];
      if(a&&a.length>1){
        // this.input_cols=true;
        // if(!/^\d+(\.\d{1,6})?$/.test(_this.money))
        var len = c.split('.')[0].length+1
        _this.money=Number(c.substring(0,len+1));
        console.log(len)
         _this.$toast(this.$t("message.f5_20"));
           return
      }
      
    },
    getTimeFun(time){
      if(time>0){
   var nowtime = new Date();
    var lefttime = time*1000 - nowtime.getTime();//距离结束时间的毫秒数
    return lefttime;
      }else{
         return 0;
      }
    
    },
       withdraw: function () {
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
    //  if(this.dogesInfo.info[10]=0){
    //    this.$toast(_this.$t("message.f5_21"));
    //    return
    //  }
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.poolContract.methods
        .withdraw()
        .send({ from: account })
        .then((res) => {
          console.log("withdraw", res);
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    investIn(){
      var _this=this;
      var account =this.account;
      if (this.dogesInfo == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      if(!this.money){
        this.$toast(_this.$t("message.f5_22"));
        return;
      }
      if(this.money>(0.5-Web3.utils.fromWei(_this.dogesInfo.info[8], 'ether'))){
        this.$toast(_this.$t("message.f5_23"));
        return;
      }
      console.log((_this.money*10**18) / this.dogesInfo.info[7],'2222222222222222222')
         this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(_this.money*10**18)
      // return
          this.poolContract.methods
        .investIn()
        .send({ from: account ,value:_this.money*10**18})
        .then((res) => {
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.f5_16"));
            _this.add_DOGESpool();
            _this.add_dogCoinContract();
          // _this.add_lpCoinContract();

          _this.money = 0;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

    }
  },
  mounted() {
    var lang = localStorage.getItem("lang");
    this.changeLanguage(lang || "en");


    var getData = () => {
    //     this.add_DOGESpool();
    // this.add_dogCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.dogesInfo || !this.Balance || !this.FIVEBalance || !this.poolContract) {
          getData();
        } else {
          // this.getCoinBalance();
          // this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();

    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 30000);
  },
    destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

